<template>
  <div>
    <v-row>
      <v-col cols="12">
        <v-btn color="accent" @click="showCreateTeacherDialog = true">
          New Teacher
        </v-btn>
      </v-col>
    </v-row>

    <v-card outlined raised>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="teacherSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        >
        </v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="teachers"
        :search="teacherSearch"
        show-select
        @click:row="goToTeacherProfile"
        v-model="selectedTeachers"
      >
      </v-data-table>
    </v-card>

    <v-dialog v-model="showCreateTeacherDialog" max-width="60vw">
      <v-card dark>
        <v-card-title>
          <v-row>
            <v-col cols="4">
              <v-textarea
                rows="1"
                label="First Name"
                v-model="firstName"
                outlined
                auto-grow
                full-width
              >
              </v-textarea>
            </v-col>
            <v-col cols="4">
              <v-textarea
                rows="1"
                label="Middle Name"
                v-model="middleName"
                outlined
                auto-grow
                full-width
              >
              </v-textarea>
            </v-col>
            <v-col cols="4">
              <v-textarea
                rows="1"
                label="Last Name"
                v-model="lastName"
                outlined
                auto-grow
                full-width
              >
              </v-textarea>
            </v-col>
          </v-row>
          <v-col cols="12">
            <v-date-picker
              v-model="birthday"
              full-width
              light
              header-color="green"
              
            ></v-date-picker>
          </v-col>
        </v-card-title>
        <v-card-actions>
          <v-spacer> </v-spacer>
          <v-btn
            color="success"
            @click="createTeacher"
            :disabled="firstName.length < 2 || lastName.length < 2"
            >Create</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import TeacherService from "../services/TeacherService";
export default {
  data: () => ({
    dialog: false,
    headers: [
      {
        text: "First Name",
        align: "start",
        value: "firstName"
      },
      {
        text: "Middle Name",
        value: "middleName"
      },
      {
        text: "Last Name",
        value: "lastName"
      },
      { text: "Group 1", value: "group1" },
      { text: "Group 2", value: "group2" },
      { text: "Group 3", value: "group3" },
      { text: "Actions", value: "action", sortable: false }
    ],
    teachers: [],
    selectedTeachers: [],
    teacherSearch: "",
    firstName: "",
    middleName: "",
    lastName: "",
    birthday: new Date().toISOString().substr(0, 10),
    showCreateTeacherDialog: false
  }),
  created() {
    SchoolService.GetTeachers(1).then(teachers => {
      this.teachers = teachers;
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.teachers.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.teachers.splice(index, 1);
    },
    createTeacher() {
      TeacherService.CreateTeacher({
        firstName: this.firstName,
        middleName: this.middleName,
        lastName: this.lastName
      }).then(() => {
        this.$router.go();
      });
    }
  }
};
</script>