import axios from 'axios'

const url = "/api"

class SchoolService {
    static async Restart() {
        const response = await axios.post(url + '/test/restart')
        return response.data
    }

    static async GetStudents(schoolId) {
        const response = await axios.get(url + '/schools/' + schoolId + '/students')
        return response.data
    }

    static async GetTeachers(schoolId) {
        const response = await axios.get(url + '/schools/' + schoolId + '/teachers')
        return response.data
    }

    static async GetCourses(schoolId) {
        const response = await axios.get(url + '/schools/' + schoolId + '/courses')
        return response.data
    }

    static async GetOffers(schoolId) {
        const response = await axios.get(url + '/schools/' + schoolId + '/offers')
        return response.data
    }

    static async GetHomeworks(schoolId) {
        const response = await axios.get(url + '/schools/' + schoolId + '/homeworks')
        return response.data
    }

    static async CreateSchool(schoolBody) {
        const response = await axios.post(url + '/schools/', schoolBody)
        return response.data
    }

    static async DeleteSchool(schoolId) {
        const response = await axios.delete(url + '/schools/' + schoolId)
        return response.data
    }
}

export default SchoolService;